import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import {PremiumModal} from '../../modals/premium/premium';


@Component({
    selector: 'lists',
    templateUrl: 'lists.html'
})

export class ListsModal {
    
    public lists: any;
    public properties: any;
    public newList: any;
    
    constructor(public platform: Platform, public params: NavParams,  public events: Events, public modalCtrl: ModalController, public viewCtrl: ViewController, public storage: Storage) {
        this.properties = {list : params.data.list, premium:false};
        this.newList = "";
        this.lists = ["default"];
        this.storage.get('lists').then((data) => {
            if (data){
                this.lists = data;
            }
        })    
        this.storage.get('premium').then((data) => {
            if (data){
                this.properties.premium = data;
            }
        }) 
        
        events.subscribe('premium:purchased', () => {
            this.properties.premium = true;
        })        
                
    }
    
    
    public openPremiumModal() {
        let modal = this.modalCtrl.create(PremiumModal);
        modal.onDidDismiss(data => {
            if (!data){return;}
            this.properties.premium = data;
            this.storage.set('premium', data);   
            this.events.publish('premium:purchased');
        })        
        modal.present();
    }      
    
    public addList(ev, newList){
        ev.stopPropagation();
        ev.preventDefault();      

        if (!newList){return;}
        
        if (this.lists.length > 3 && !this.properties.premium){
            this.openPremiumModal();
            return;
        }
        
        
        this.lists.push(newList);
        this.storage.set("lists", this.lists);
        this.newList = "";
    }
    
    public removeList(ev,index){
        ev.preventDefault();
        ev.stopPropagation();        
        this.lists.splice(index,1);
        this.storage.set("lists", this.lists);
    }  
    
    public selectList(list){
        this.properties.list = list;
        this.viewCtrl.dismiss(this.properties.list);
    }  
    

    dismiss() {
        this.viewCtrl.dismiss(false);
    }
}



