import { Component } from '@angular/core';
import { NavController, ModalController, NavParams, AlertController, Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import {PremiumModal} from '../../modals/premium/premium';

import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
import { EmailComposer } from '@ionic-native/email-composer';
import { InAppBrowser } from '@ionic-native/in-app-browser';


@Component({
  selector: 'page-submission',
  templateUrl: 'submission.html'
})
export class SubmissionPage {
    
    public submissions:any;
    public properties: any;
    
    constructor(public navCtrl: NavController, public events: Events, public storage: Storage, public modalCtrl: ModalController, public params: NavParams, private file: File, public alertCtrl: AlertController, private emailComposer: EmailComposer, private fileOpener: FileOpener, private iab: InAppBrowser) {

        this.submissions = [];
        this.properties = {list:params.data.list, premium:true};
        
        this.storage.get("submissions").then((data) => {
            if (data){
                for (var index in data){
                    if (index === this.properties.list){
                        this.submissions = data[index];
                    }
                }
            }
        })
        
        this.storage.get('premium').then((data) => {
            if (data){
                this.properties.premium = data;
            }
        })   
        
        
        events.subscribe('premium:purchased', () => {
            this.properties.premium = true;
        })   
        
    }
    
    
    ionViewDidEnter() {
        this.storage.get("submissions").then((data) => {
            if (data){
                for (var index in data){
                    if (index === this.properties.list){
                        this.submissions = data[index];
                    }
                }
            }
        })     
    }     
    
    
    
    
    public getKeys(item){
        return Object.keys(item);
    }
    
    
    public openPremiumModal() {
        let modal = this.modalCtrl.create(PremiumModal);
        modal.onDidDismiss(data => {
            if (!data){return;}
            this.properties.premium = data;
            this.storage.set('premium', data);
            this.events.publish('premium:purchased');   
        })        
        modal.present();
    }    
    
    
    
    public saveFile() {
        if (!this.properties.premium){
            this.openPremiumModal();
            return;
        }
        var csv: any = this.convertToCSV();
        
        
        let fileName = this.properties.list + ".csv";
        
        let fullPath = this.file.dataDirectory + fileName;
        
        console.log(fullPath);
        
        
        this.file.writeFile(this.file.dataDirectory, fileName, csv, {replace:true}).then(() => {
            const alert = this.alertCtrl.create({
                title: 'List saved',
                subTitle: "You can find the file in your data directory",
                buttons: [
                {
                    text: 'Cancel',
                    role: 'OK',
                    handler: () => {}
                },
                {
                    text: 'Open',
                    handler: () => {
                        this.fileOpener.open(fullPath, "text/csv");
                    }
                }
            ]
            });
            alert.present();  

            
        }).catch(
            err => {
                this.file.writeExistingFile(this.file.dataDirectory, fileName, csv).then(() => {
                    const alert = this.alertCtrl.create({
                        title: 'List saved',
                        subTitle: "You can find the file in your data directory",
                        buttons: [
                            {
                                text: 'Cancel',
                                role: 'OK',
                                handler: () => {}
                            },
                            {
                                text: 'Open',
                                handler: () => {
                                    this.fileOpener.open(fullPath, "text/csv");
                                }
                            }
                        ]
                    });
                    alert.present();  
                }).catch(
                    err => {
                        const alert = this.alertCtrl.create({
                            title: 'Failed',
                            subTitle: "Couldn't saved this list. Please try again.",
                            buttons: ['OK']
                        });
                        alert.present();  
                    })
            })
    }

    convertToCSV() {
        let csv: any = ''
        let line: any = ''
        
        let headers = Object.keys(this.submissions[0]);

        //Header
        for (let header of headers) {
            if (line !== ''){ line += ',';}
            line += header;
        }
        csv += line + '\r\n';

        //Submissions
        for (let submission of this.submissions) {
            line = '';
            for (let header of headers){
                if (line != '') line += ','
                line += submission[header];
                
                
            }

            csv += line + '\r\n'
        }

        return csv
    }  
    
    
    public sendEmail(){
        if (!this.properties.premium){
            this.openPremiumModal();
            return;
        }
                
        let email = {
          to: [],
          subject: 'Hi Everyone',
          body: 'Just an update to let you know...',
          isHtml: true
        };        
        
        let stringEmails = ""
        for (let submission of this.submissions){
            if (submission["email"] && submission["email"].indexOf("@")){
                email.to.push(submission["email"]);
            }
            stringEmails = stringEmails + (submission["email"] && submission["email"].indexOf("@") > -1 ? submission["email"] + "," : "");
        }
        
        
  
        stringEmails.replace(/(^,)|(,$)/g, "");
        
        
        alert(stringEmails);

        this.emailComposer.open(email);
        
    }
      
    
    public confirmDelete(index){
        let alert = this.alertCtrl.create({
            title: 'Delete Submission',
            message: 'Are you sure you want to remove this submission?',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {}
                },
                {
                    text: 'Yes',
                    handler: () => {
                        this.submissions.splice(index,1);
                        this.storage.get("submissions").then((data) => {
                            if (data){
                                for (var index in data){
                                    if (index === this.properties.list){
                                        data[index] = this.submissions;
                                        this.storage.set("submissions",data); 
                                        break;
                                    }
                                }
                            }
                        })
                    }
                }
            ]
        });
        alert.present();        
    }
    
    public isNotLink(field){
        return !(this.isLink(field) || this.isPhone(field) || this.isEmail(field));
    }
    
    public isLink(field){
        field = field.toLowerCase();
        if (field.indexOf("website") > -1 || field.indexOf("url") > -1){
            return true;
        }
        return false;
    }
    
    
    public isPhone(field){
        field = field.toLowerCase();
        if (field.indexOf("phone") > -1 || field.indexOf("mobile") > -1){
            return true;
        }
        return false;        
    }    
    
    
    public isEmail(field){
        field = field.toLowerCase();
        if (field.indexOf("email") > -1){
            return true;
        }
        return false;
    }   
    
    public openLink(link){
        console.log(link);
        this.iab.create(link, "_system")
    }     
}



