import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
import { EmailComposer } from '@ionic-native/email-composer';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { InAppPurchase } from '@ionic-native/in-app-purchase';
import { MyApp } from './app.component';

import { SubmissionsPage } from '../pages/submissions/submissions';
import { SubmissionPage } from '../pages/submission/submission';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import {AddFieldModal} from '../modals/fields/add-field';
import {EditFieldModal} from '../modals/fields/edit-field';
import {ListsModal} from '../modals/lists/lists';
import {PremiumModal} from '../modals/premium/premium';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Camera, CameraOptions } from '@ionic-native/camera';



@NgModule({
  declarations: [
    MyApp,
    SubmissionsPage,
    SubmissionPage,
    HomePage,
    TabsPage,
    AddFieldModal,
    EditFieldModal,
    ListsModal,
    PremiumModal
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    SubmissionsPage,
    SubmissionPage,
    HomePage,
    TabsPage,
    AddFieldModal,
    EditFieldModal,
    ListsModal,
    PremiumModal
  ],
  providers: [
    StatusBar,
    SplashScreen,
    File,
    FileOpener,
    EmailComposer,
    InAppPurchase,
    InAppBrowser,
    Camera,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}






