import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, AlertController, Events } from 'ionic-angular';
import {AddFieldModal} from '../../modals/fields/add-field';
import {EditFieldModal} from '../../modals/fields/edit-field';
import {ListsModal} from '../../modals/lists/lists';
import {PremiumModal} from '../../modals/premium/premium';
import { Storage } from '@ionic/storage';
import { InAppPurchase } from '@ionic-native/in-app-purchase';

import { Camera, CameraOptions } from '@ionic-native/camera';



@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})


export class HomePage {
    
    public fields: any;
    public submission: any;
    public properties: any;
    public tabbar: any;
    
    
    
    constructor(public navCtrl: NavController, public events: Events, public modalCtrl: ModalController, public storage: Storage, public alertCtrl: AlertController, private camera: Camera) {
        this.tabbar = document.querySelector("#tab-navigation .tabbar");
        
        this.fields = [];
        this.storage.get('fields').then((data) => {
            if (data){
                this.fields = data;
            }
            else{
                this.setupFields();
            }
        })
       
        this.submission = {};
        
        this.properties = {list:"default", live:false, background:"assets/img/background.jpg", logo:"assets/img/logo.png", intro:"Please answer the following questions", premium:false};

        this.storage.get('currentList').then((data) => {
            if (data){
                this.properties.list = data;
            }
        })   
        
        this.storage.get('isLive').then((data) => {
            if (data){
                this.properties.live = data;
                if (this.properties.live){
                    this.tabbar.style.display = 'none';
                }
            }
        })  
        
        this.storage.get('backgroundImage').then((data) => {
            if (data){
                this.properties.background = data;
            }
        })   
        
        
        this.storage.get('logoImage').then((data) => {
            if (data){
                this.properties.logo = data;
            }
        })  
        
        this.storage.get('introMessage').then((data) => {
            if (data){
                this.properties.intro = data;
            }
        })  
        
        
        this.storage.get('premium').then((data) => {
            if (data){
                this.properties.premium = data;
            }
        })         
                                                
        events.subscribe('premium:purchased', () => {
            this.properties.premium = true;
        })
        
                
    }
    
    
    private setupFields(){
        let fields = [
            {
                name:"name",
                label:"What is your name?",
                inputType: "text",
                required:true,
                order:1
            },
            {
                name:"email",
                label:"Email Address",
                inputType: "email",
                required:true,
                order:2
            },
            {
                name:"move",
                label:"When do you need to move?",
                inputType: "select",
                options: [{value:"0-3 months"}, {value:"4-8 months"}, {value:">6 months"}],
                required:false,
                order:3
            }            
        ]
        
        this.storage.set('fields', fields);
        
        this.fields = fields;
    }

    public openPremiumModal() {
        let modal = this.modalCtrl.create(PremiumModal);
        modal.onDidDismiss(data => {
            if (!data){return;}
            this.properties.premium = data;
            this.storage.set('premium', data);   
            this.events.publish('premium:purchased');
        })        
        modal.present();
    }  
    

        
    public openAddFieldModal() {
        if (this.fields.length > 3 && !this.properties.premium){
            this.openPremiumModal();
            return;
        }
        let modal = this.modalCtrl.create(AddFieldModal);
        modal.onDidDismiss(data => {
            if (!data){return;}
            this.fields.push(data);
            this.storage.set('fields', this.fields);   
        })        
        modal.present();
    }  
    
    
    public openEditFieldModal(field) {
        if (this.properties.live){return;}
        let modal = this.modalCtrl.create(EditFieldModal, JSON.parse(JSON.stringify(field)));
        modal.onDidDismiss(data => {
            if (!data){return;}
            for (var index in this.fields){
                if (this.fields[index].name === data.name){
                    this.fields[index] = data;
                }
            }

            this.storage.set('fields', this.fields);           
        })        
        modal.present();
    }
    
    public openRemoveField(ev,index){
        ev.preventDefault();
        ev.stopPropagation();
        let alert = this.alertCtrl.create({
            title: 'Confirm',
            message: 'Are you sure you want to remove this field?',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {}
                },
                {
                    text: 'Yes',
                    handler: () => {
                        this.fields.splice(index,1);
                        this.storage.set("fields", this.fields);
                    }
                }
            ]
        });
        alert.present();        
    }    
    
    public reorderFields(indexes) {
        let element = this.fields[indexes.from];
        this.fields.splice(indexes.from, 1);
        this.fields.splice(indexes.to, 0, element);
        for (var index in this.fields){
            this.fields[index].order = index + 1;
        }
        this.storage.set("fields", this.fields);
    }     
    
    public openChangeListModal() {
        let modal = this.modalCtrl.create(ListsModal, {list : this.properties.list});
        modal.onDidDismiss(data => {
            if (!data){return;}
            this.properties.list  = data;      
            this.storage.set("currentList", this.properties.list);   
        })        
        modal.present();
    }    
    
   
    saveToList(submissions){
        if (submissions[this.properties.list]){
            submissions[this.properties.list].push(this.submission);
        }
        else{
            submissions[this.properties.list] = [this.submission];
        }
        
        let submission = this.submission;
        let fields = this.fields;
        this.storage.set('submissions', submissions).then(function(){
            submission = {};
            for (let field of fields){
                field.enteredValue = "";
            }            
        });
        

        
    }
    
    public saveSubmission() {
        
        for (let field of this.fields){

            if (field.required && !field.enteredValue){
                
                const alert = this.alertCtrl.create({
                    title: 'Fields Required',
                    subTitle: "You havn't entered information into a field which is required. Please review and submit again.",
                    buttons: ['OK']
                });
                alert.present();                
                
                this.submission = {};
                return;
            }
            else if (field.name){
 
                this.submission[field.name] = field.enteredValue;
           
            }
        }
        
        this.storage.get('submissions').then((data)=> {
            if (data){
                this.saveToList(data);
            }
            else{
                this.saveToList({});
            }           
        });
        
        const alert = this.alertCtrl.create({
            title: 'Thank You!',
            subTitle: "Your information has been saved.",
            buttons: ['OK']
        });
        alert.present();         
        
        
            
    }
    
    public goLive(){
        let alert = this.alertCtrl.create({
            title: 'Confirm',
            message: 'Are you sure you want to go live?',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {}
                },
                {
                    text: 'Yes',
                    handler: () => {
                        this.properties.live = true;
                        this.storage.set("isLive", true);
                        this.tabbar.style.display = 'none';
                    }
                }
            ]
        });
        alert.present();        
    }
    
    public goEditMode(){
        
        for (let field of this.fields){
            field.enteredValue = "";
            this.submission = {};
            
        }
        
        this.properties.live = false;
        this.storage.set("isLive", false);   
        
        this.tabbar.style.display = 'flex';    
    }

    public setBackground(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.DATA_URL,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 100
        }

        this.camera.getPicture(cameraOptions)
            .then((imageData) => {
                //alert(imageData);
                let base64Image = 'data:image/jpeg;base64,' + imageData;
                this.properties.background = base64Image;
                this.storage.set("backgroundImage", base64Image);
            }, 
            err => console.log(err));   
    }    
    
    public setLogo(){
        if (this.properties.live){return;}
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.DATA_URL,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 100
        }

        this.camera.getPicture(cameraOptions)
            .then((imageData) => {
                let base64Image = 'data:image/jpeg;base64,' + imageData;
                this.properties.logo = base64Image;;                
                this.storage.set("logoImage", base64Image);
            }, 
            err => console.log(err));   
    }    
    
    public openEditIntro(){
        if (this.properties.live){return;}
        const alert = this.alertCtrl.create({
          title: 'Intro Message',
            inputs: [
                {
                    name: 'message',
                    placeholder: 'Enter Intro Message',
                    value: this.properties.intro
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {}
                },
                {
                    text: 'Save',
                    handler: data => {
                        if (data.message) {
                            this.properties.intro = data.message;
                            this.storage.set("introMessage", data.message);
                        }
                    }
                }
          ]
        });
        alert.present();      
    }        
}





