import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { SubmissionPage } from '../../pages/submission/submission';

@Component({
  selector: 'page-submissions',
  templateUrl: 'submissions.html'
})
export class SubmissionsPage {
    
    public submissions:any;
    
    constructor(public navCtrl: NavController, public storage: Storage, public alertCtrl: AlertController) {
        
        this.submissions = [];

        
    }
    
    ionViewDidEnter() {
        this.storage.get("submissions").then((data) => {
            if (data){
                console.log(data);
                this.submissions = Object.keys(data);
            }
        })       
    }    
    
    public selectGroup(list){
        this.navCtrl.push(SubmissionPage, {list:list});
    }
    
    public confirmDelete(index, listName){
        let alert = this.alertCtrl.create({
            title: 'Delete Submissions',
            message: 'Are you sure you want to remove all submissions for this list?',
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: () => {}
                },
                {
                    text: 'Yes',
                    handler: () => {
                        
                        this.submissions.splice(index,1);                       
                        
                        this.storage.get("submissions").then((data) => {
                            if (data){
                                
                                delete data[listName];
                                this.storage.set("submissions",data); 
 
                            }
                        })
                    }
                }
            ]
        });
        alert.present();        
    }    
    
    

}



